.success{
  border: 2px solid green;
  color: green;
  border-radius: 10px;
  padding: 5px;
  background-color: rgba(223, 223, 223, 0.562);
}
.fail{
  border: 2px solid red;
  color: red;
  border-radius: 10px;
  padding: 5px;
  background-color: rgba(223, 223, 223, 0.562);
}
.message{
  width: 95%;
  margin: 0 auto;
}
.app_container{
  width: 400px;
  margin: 10px auto;
  text-align: center;
  border: 1px solid rgb(54, 54, 54);
  padding: 30px;
  min-height: 85vh;
  border-radius: 5px;
  background-color: white;
}
body{
  background-color: #fafafa;
}